:root{--text-color: rgb(48 105 165)}.home_page{position:relative;width:75%;left:50%;transform:translateX(-50%)}.home_page .head,.home_page .body{position:relative;width:80%;left:50%;transform:translateX(-50%)}.home_page .head{padding-top:40px;display:flex}.home_page .head .avt .avatar{width:240px;height:300px;border-radius:12px;background-image:url("./avt.jpg");background-size:cover}.home_page .head .info{margin-left:50px}.home_page .head .info .name{text-transform:uppercase;font-size:2rem;font-weight:700;color:var(--text-color)}.home_page .head .info .word{font-size:1.4rem;color:var(--text-color)}.home_page .head .icon:before{content:"‚Äù";font-size:5rem;color:#7eb9f8}.home_page .head .content .item{list-style:none;font-size:1.2rem;font-style:italic;color:var(--text-color)}.home_page .body{display:flex}.home_page .body .left{flex:3}.home_page .body .right{flex:5}.home_page .body .left .info,.home_page .body .right .info{margin-top:50px}.home_page .body .left .info .title,.home_page .body .right .info .title{font-size:1.6rem;font-weight:700;color:var(--text-color)}.home_page .body .left .info .information,.home_page .body .right .info .information{margin-top:10px}.home_page .body .left .info .information .item,.home_page .body .right .info .information .item{display:flex;color:var(--text-color)}.home_page .body .left .info .information .item .icon,.home_page .body .right .info .information .item .icon{display:flex;align-items:center;font-size:1.6rem}.home_page .body .left .info .information .item .text,.home_page .body .right .info .information .item .text{margin-left:10px;font-style:italic}.home_page .body .left .info .skill .item,.home_page .body .right .info .skill .item{color:var(--text-color);font-style:italic}